import { Component, OnInit,Input,Output,EventEmitter } from '@angular/core';

@Component({
  selector: 'app-child',
  templateUrl: './child.component.html',
  styleUrls: ['./child.component.css']
})
export class ChildComponent implements OnInit {

@Input() productData;

@Output() subRating= new EventEmitter;
@Output() subReview= new EventEmitter;

subRate(r){
this.productData.rating=r;
this.subRating.emit(this.productData.rating);
}

review1(a){
this.productData.review=a;
this.subReview.emit(this.productData.review);
}

  constructor() { }

  ngOnInit() {
  }

}
